import React from "react";


export class Splash extends React.Component {
    constructor(props){
        super(props);
        this.state={
            randomDescriptor:<><b className="fadedshort" style={{color: '#fff'}} key={0}>Development</b></>,
            randomItem:<><b className="fadedshort" style={{color: '#fff'}} key={0}>On Time</b></>,
            randomIndex:0,
        }

    }
    componentDidMount() {
        setInterval(()=>{this.renderAdjective()},2500)
        setInterval(()=>{this.renderDescriptor()},2500)

    }
    renderAdjective(){
        const items = ['On Time ','Under Budget ','Stress free','Anywhere in the world (Global)','That provides class leading UX','To beat the competition','To bring in qualified leads', 'Analyse and serve your customer base'];
        const randomIndex = Math.floor(Math.random()*items.length);

        return this.setState({randomItem:<><b className="fadedshort" style={{color: '#1228b3'}} key={randomIndex}>{items[randomIndex]}</b></>})
    }
    renderDescriptor(){
        const items = ['Development','Design', 'Branding', 'Publishing', 'Auditing','Consulting','Architecture','Solutions'];
        const randomIndex = Math.floor(Math.random()*items.length);

        return this.setState({randomDescriptor:<><b className="fadedshort" style={{color: '#1228b3'}} key={randomIndex}>{items[randomIndex]}</b></>})
    }


    render() {
        return (<>
            <div className="cmImageBackground" style={{height: '66vh', width: '100%', backgroundColor: 'rgba(105,140,170,0.75)', color: '#fff',borderBottom:'5px solid #505050'}}>
                <div style={{display: 'flex', justifyContent: 'center',paddingTop:140}}>
                    <div className="bg-blur" style={{width:600,backgroundColor:'#505050',padding:60,paddingBottom:0,paddingTop:30,height:300}}>
                        <h2>Cache More {this.state.randomDescriptor} </h2>
                        <h4>is currently developing...</h4>
                        <p className="clickableLinkToDemo"> LifeTime Roofing demo page</p>
                        <p className="clickableLinkToDemo" onClick={() => {
                            window.location.href = '/dreamwoodhomes'
                        }}>Dreamwood Homes demo page</p>
                        <p className="clickableLinkToDemo">Akita Inu ASA NFT marketing website</p>
                        <div style={{marginBottom:40}}><a className="book" href={'mailto:cashmore.alex@gmail.com'}><b>Book a consult.</b></a> publishing slots remaining in Q2 and Q4 2022.</div>
                    </div>

                </div>

            </div>
            <div style={{backgroundColor: '#131265', color: '#505050', height: '34vh', paddingLeft: 30,paddingTop:20}}>
                <p style={{fontSize:20}}>Need a website? {this.state.randomItem}</p>
                <p>Design // UX // Development</p>
                <p>Web Apps and Integrations</p>
                <p>Solution Architecture</p>
                <p>Merchandising, Branding, SEO</p>
                <p>Analytics and Insights</p>
                <p><a className="book" href={'mailto:cashmore.alex@gmail.com'}><b>Book a consult.</b></a> </p>
            </div>

        </>)
    }

}